import React, { useEffect, useState } from 'react'
import {motion} from "framer-motion"

export default function ImageSlider({images, title, slogen}) {
  //const [current_image, set_current_image] = useState("") // ez egy képet tárol, már nem lesz szükség rá a motionnál
  const [image_index, set_image_index] = useState(0)   // folyamatosan lépked egy képlistában
  let _index = 0

  // set first image - erre sem lesz szükség motionnál
  //useEffect(() => {
    //set_current_image(images[image_index])
  //}, [images, image_index])   // lehet két függősége ennek az effectnek két ok is ami miatt lefut

  // index léptetése

  useEffect(() => {
    const interval = setInterval(() => {
      _index ++;
      if(_index >= images.length){
        _index = 0
      }
      set_image_index(_index)
    }, 5000)
    return() => clearInterval(interval)
  }, [images])

  //<div className='image-slider-container' style={{backgroundImage: `url(${images[0]})`}}>
  //  {/*<img src={images[0]} alt="" />*/}
  //<h1>{title}</h1>
  // <h2>{slogen}</h2>
  //</div>
  //innen is ki kell venni a current_image
  return (
    <div className='image-slider-container'>
        {/* <img src={current_image} alt="" /> */   }
        {
          images.map((image_path, index) => <img className={`${index === image_index? "visible":""}`} key={index} src={image_path}/>)
        }
        <motion.h1 initial={{opacity:0, y:-200}} animate={{opacity:1, y:0}} transition={{delay:1}}>{title}</motion.h1>
        <motion.h2 initial={{opacity:0, y:200}} animate={{opacity:1, y:0}} transition={{delay:1}}>{slogen}</motion.h2>
    </div>
  )
}
